*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

html {
  font-family: "Amiri", serif;
  font-size: 62.2%;
  box-sizing: border-box;
}
@media screen and (max-width: 1200px) {
  html {
    font-size: 50%;
  }
}

body {
  display: grid;
  grid-template-columns: [full-start] minmax(2rem, 1fr) [center-start] repeat(8, [col-start] minmax(min-content, 15rem) [col-start]) [center-end] minmax(2rem, 1fr) [full-end];
}

.body {
  grid-column: center-start/center-end;
  display: grid;
  grid-template-columns: 25% 1fr;
  gap: 1.5rem;
}
@media screen and (max-width: 1200px) {
  .body {
    grid-template-columns: 1fr;
  }
}

.nav {
  grid-column: center-start/center-end;
  display: flex;
  justify-content: space-between;
  margin-top: 5rem;
  margin-bottom: 2rem;
  align-items: center;
}
.nav ul, .nav li {
  list-style: none;
}
.nav .nav__logo-img {
  width: 65px;
}
.nav__search {
  cursor: pointer;
}
.nav .custom-wordpres-dropdown {
  width: 100%;
}
.nav .custom-wordpres-dropdown .menu-header-menu-container {
  width: 100%;
  height: 100%;
}
.nav .custom-wordpres-dropdown ul {
  display: grid;
  align-items: center;
  height: 100%;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
}
.nav .custom-wordpres-dropdown ul li:hover:after {
  opacity: 1;
  top: -6px;
}
.nav .custom-wordpres-dropdown ul li:after {
  transition: all 0.4s ease-in-out;
  content: "";
  background-color: #64ccf3;
  width: 100%;
  height: 3px;
  border-radius: 3px;
  display: block;
  position: relative;
  top: -26px;
  opacity: 0;
}
.nav .custom-wordpres-dropdown ul li a {
  color: #111948;
  font-size: 3.8rem;
  text-decoration: auto;
}
.nav .nav__search {
  display: flex;
}
.nav .nav__search img {
  width: 50px;
}

.hero {
  grid-column: full-start/full-end;
  height: 490px;
  overflow: hidden;
  margin-top: 2rem;
}
@media screen and (max-width: 690px) {
  .hero {
    height: 240px;
    margin: 2rem;
  }
}
@media (min-width: 700px) {
  .hero__img {
    height: 490px;
  }
}
.hero__img img {
  height: 100%;
  width: 100%;
  object-position: 0 0;
  position: relative;
}
.hero__img img::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  background-color: red;
}
.hero__cover {
  background-color: #64CCF3;
}
.hero__content {
  position: absolute;
  top: 0;
  width: 100%;
  height: 490px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 2;
  direction: rtl;
}
@media (max-width: 580px) {
  .hero__content {
    top: -100px;
  }
}
.hero h2 {
  font-size: 3.3rem;
  background: rgba(17, 25, 72, 0.82);
  padding: 2rem;
  border-radius: 25px 1px;
  width: 474px;
  box-shadow: 2px 1px 11px 1px rgba(100, 204, 243, 0.26);
}
@media (max-width: 580px) {
  .hero h2 {
    font-size: 2em;
    width: 274px;
  }
}
.hero h2 a {
  color: #fff;
  text-decoration: none;
}
.hero h2 a:hover {
  text-decoration: underline;
  text-decoration-color: #64CCF3;
}
.hero__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(17, 25, 72, 0.53);
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}

.owl-dots {
  position: absolute;
  top: 60vh;
  right: 47%;
}
@media (max-width: 580px) {
  .owl-dots {
    top: 48vh;
  }
}

.owl-carousel button.owl-dot {
  border: 2px solid #9400d3;
  width: 21px;
  height: 21px;
  border-radius: 50%;
  margin-left: 1rem;
  box-shadow: 0px 0px 6px 1px rgba(100, 204, 243, 0.8117647059);
  position: relative;
}

.owl-dot.active {
  background-color: rgba(1, 11, 19, 0.7215686275);
}
.owl-dot.active::after {
  content: "";
  position: absolute;
  top: 7px;
  left: 6.8px;
  width: 8px;
  height: 8px;
  background-color: #64ccf3;
  border-radius: 50%;
  box-shadow: 1px 1px 0px rgba(0, 0, 0, 0.6588235294);
}

.owl-theme .owl-dots .owl-dot span {
  width: 15px !important;
  height: 15px !important;
  border: 3px solid #111948;
  position: relative;
  left: -6.9px;
  top: -5px;
  background: transparent !important;
}

.owl-carousel.owl-loaded {
  direction: ltr;
  display: grid !important;
}

